* {
    box-sizing: border-box;
    margin: 0;
    padding: 0; }

body {
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    line-height: 24px;
    background-color: #F9FAFB; }

a:link {
    color: #5995DA;
    text-decoration: none; }

a:visited {
    color: #407FC7; }

a:hover,
a:visited:hover {
    color: #76AEED;
    border-bottom: 1px solid #76AEED; }

a:active,
a:visited:active {
    color: #5995DA; }
p {
    font-size: 16px;
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    display: block;
    margin: 0 0 10px 0;
    text-indent: 10px;
    line-height: 24px; }


.head {
    text-align: center; }
.main-nav {
    font-size: 14px;
    line-height: 26px;
    color: #5D6063;
    background-color: #FFFFFF;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    box-shadow: 0 2px 0 rgba(93, 96, 99, .1);
    width: 100%;
    height: 40px;
    padding: 0 13px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center; }


.main-nav__branding {
    display: flex;
    justify-content: flex-start;
    align-items: center; }
.main-nav__logo-link:visited:hover,
.main-nav__logo-link:hover {
  border-bottom: none; }

.main-nav__logo-link {
    margin-right: 20%;
    img {
        max-width: 7vw; } }

.main-nav__name {
  position: relative;
  top: 1px;
  left: 4%;
  text-transform: uppercase; }

.main-nav__html-css {
  position: relative;
  top: 1px; }

.page {
    margin: 20px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 850px; }

.book {
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 800px;
    color: #5D6063;
    p {
        text-indent: none; } }

.chapter, .attention {
    margin: 0 2%; }
.attention {
    text-align: left;
    background-color: #383a3d1a;
    border: 2px solid #8d8f9152;
    margin: 30px 0;
    border-radius: 3%; }

h1, h2,h3,h4 {
    display: block;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em; }


h1 {
    font-size: 1.5em; }

h2 {

    font-size: 1.5em; }

h3 {

    font-size: 1.3em;
    margin-block-start: 0.83em;
    margin-block-end: 0.83em;
    line-height: 1.4em; }

h4 {

    font-size: 1.2em;
    margin-block-start: 1em;
    margin-block-end: 1em;
    line-height: 1.4em; }

table {
    font-size: 16px;
    vertical-align: middle;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    border: 2px solid grey;
    margin: 0;
    border-collapse: collapse;

    tr {
        border: 1px solid grey; }
    td {
        word-wrap: break-word;
        margin: 0;
        border: 2px solid grey; } }





.quote_from_diffrent_book {
    text-align: right;
    font-style: italic; }

.img {
    display: flex;
    flex-direction: column;
    font-style: italic;
    text-align: center;
    margin-bottom: 1em;
    img {
        max-width: 100%; } }

ul, ol {
    margin-bottom: 1em;
    margin-left: 1em; }

.quote {
    border: 1px solid black;
    padding: 5px;
    font-style: italic;
    margin: 10px 0 10px 0;
    img {
        max-width: 12vw; }
    p {
        margin: 0; } }

.link_to_pspo {
    li a {
        display: block;
        background: #ececed;
        padding: 10px;
        font-size: 14px;
        color:#454547 {}
        text-decoration: none;
        text-transform: uppercase;
        transition: all 0.3s ease;
        max-width: auto;
        border: 1px solid #c7c8ca; }

    ul {
        list-style: none;
        display: flex;
        justify-content: flex-start;
        flex-direction: row;
        flex-wrap: wrap; } }
.undone {
    color: #cec5c5; }

@media screen and (max-width:399px) {
    .link_to_pspo {
        ul {
            flex-direction: column; } }
    .quote {
        img {
            display: none; } } }

